.transport-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.transport-list__item {

  &--disabled {
    opacity: 0.15;
  }

  &:nth-child(1) {
    background-image: url(../../img/icons/plane.svg);
  }

  &:nth-child(2) {
    background-image: url(../../img/icons/bus.svg);
  }

  &:nth-child(3) {
    background-image: url(../../img/icons/bicycle.svg);
  }

  &:nth-child(4) {
    background-image: url(../../img/icons/run.svg);
  }
}
